
body {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  background-color: #000;
  min-height: 100vh;
  color: #0F0;
  font-family: 'Cascadia';
}

code {
  width: 23.8em;
  height: 23.8em;
  font-family: 'Spezia Mono';
  font-size: 1rem;
  font-variation-settings: 'wght' 0;
  border: 1px dashed #0F05;
  padding: 1em;
  overflow: hidden;
  // 'wght' 0 - 1000
  // 'mono' 0-100
}
